import { useState, useEffect, useCallback } from 'react'
import Icon from '../../components/Icon/Icon'
import { hide } from 'react-functional-modal'
import { keccak256, toUtf8Bytes } from 'ethers/lib/utils'
import './register.less'
import Button from '../../components/Button/Button'
import Emitter from '../../utils/Emitter'
import { EVENT_TRANS_BEGIN, EVENT_TRANS_END } from '../../utils/Constants'

export default function Register({ account, contract, registerAfter }) {
  const [recruiterCode, setRecruiteCode] = useState("DERI")
  const [brokerCode, setBrokerCode] = useState("")
  const [recruiterCodeKeccak256, setRecruiterCodeKeccak256] = useState("")
  const [brokerCodeKeccak256, setBrokerCodeKeccak256] = useState("")
  const [isChecking, setIsChecking] = useState(false)
  const [isBrokerLegitimate, setIsBrokerLegitimate] = useState(true)
  const [isRecruiterLegitimate, setIsRecruiterLegitimate] = useState(true)
  const [disabled, setDisabled] = useState(true)
  const [noLegitimateText, setNoLegitimateText] = useState("")
  const transactionTitle = {
    text: "Generate Code",
    icon: ""
  }
  const transactionContent = {
    success: "Generate Code",
    error: "Transaction Failed"
  }
  const onChangeBroker = (event) => {
    let { value } = event.target;
    if (value) {
      let isletter = /^[a-zA-Z]+$/.test(value);
      console.log(isletter)
      if (!isletter) {
        return false
      }
      let keccak256Data = toUtf8Bytes(value)
      let keccak256Value = keccak256(keccak256Data)
      setBrokerCodeKeccak256(keccak256Value)
    }
    console.log(value)

    setBrokerCode(value)
  }
  const onChangeRecuiter = (event) => {
    let { value } = event.target;
    console.log(value)
    if (value) {
      value = value.replace("/^[A-Za-z]+$/g", '')
      let keccak256Data = toUtf8Bytes(value)
      let keccak256Value = keccak256(keccak256Data)
      setRecruiterCodeKeccak256(keccak256Value)
    }
    setRecruiteCode(value)
  }
  const getRecruiterIsTrue = useCallback(async () => {
    if (recruiterCodeKeccak256) {
      let res = await contract.recruiterAddresses(recruiterCodeKeccak256)
      if (res !== "0x0000000000000000000000000000000000000000") {
        setIsRecruiterLegitimate(true)
      } else {
        setIsRecruiterLegitimate(false)
        setNoLegitimateText("Invalid recruiter code")
      }
      setIsChecking(false)
    }
  }, [recruiterCodeKeccak256, contract])
  const getBorkerCodeIsTrue = useCallback(async () => {
    if (brokerCodeKeccak256) {
      let res = await contract.brokerAddresses(brokerCodeKeccak256)
      if (res === "0x0000000000000000000000000000000000000000") {
        setIsBrokerLegitimate(true)
      } else {
        setIsBrokerLegitimate(false)
        setNoLegitimateText("Invalid broker code")
      }
      setIsChecking(false)
    }
  }, [brokerCodeKeccak256, contract])

  const click = useCallback(async () => {
    if (recruiterCode && brokerCode && isBrokerLegitimate) {
      let res
      try {
        res = await contract.registerBroker(brokerCode, recruiterCode)
      } catch (error) {
        return false;
      }
      if (res && res.hash) {
        Emitter.emit(EVENT_TRANS_BEGIN, {
          title: transactionTitle, content: transactionContent, hash: res.hash
        })
      } else {
        return false
      }
      const receipt = await res.wait()
      console.log("receipt", receipt)
      if (receipt) {
        console.log("receipt", receipt)
        if (receipt && receipt.transactionHash) {
          const hash = receipt.transactionHash
          if (receipt.status) {
            registerAfter()
            hide("register")
            Emitter.emit(EVENT_TRANS_END, { title: transactionTitle, content: transactionContent, context: { success: true, hash: hash, error: "error" } })
          } else {
            Emitter.emit(EVENT_TRANS_END, { title: transactionTitle, content: transactionContent, context: { success: false.success, hash: hash, error: "error" } })
          }
        }
      }
    }
  }, [recruiterCode, brokerCode, contract])

  useEffect(() => {
    if (brokerCodeKeccak256) {
      setIsChecking(true)
      getBorkerCodeIsTrue()
    }
  }, [brokerCodeKeccak256])
  useEffect(() => {
    if (recruiterCodeKeccak256) {
      setIsChecking(true)
      getRecruiterIsTrue()
    }
  }, [recruiterCodeKeccak256])

  useEffect(() => {
    if (!isBrokerLegitimate || isChecking || !brokerCode || !recruiterCode || !isRecruiterLegitimate) {
      setDisabled(true)
    } else {
      setDisabled(false)
    }
  }, [isBrokerLegitimate, isChecking, brokerCode, recruiterCode])

  return <div className='register-modal-box'>
    <div className='register-modal-header'>
      Generate Code
      <Icon token="close-modal-icon" onClick={() => hide("register")} />
    </div>
    <div className='register-modal-info'>
      <div>
        <input placeholder='ENTER YOUR CODE' value={brokerCode} onChange={onChangeBroker} />
      </div>
      <div className='register-title'>
        Broker Recruiter Code (Required)
      </div>
      <div>
        <input placeholder='ENTER YOUR CODE' value={recruiterCode} onChange={onChangeRecuiter} />
      </div>

      <div className='confirm-btn'>
        <div className='noLegitimateText'>
          {noLegitimateText}
        </div>
        <Button width="100%" disabled={disabled} label={isChecking ? "Checking..." : "CONFIRM"} onClick={click} />
      </div>
    </div>
  </div>
}